    innerBg: fitnesBg,
    innerImg: fitnesImg,
    html:
      "<blockquote class='blockquote'>  -     -,     .    4200 2.             ,   ,      .</blockquote>",
    list: [
      { id: "1", name: "", url: "" },
      { id: "2", name: " ", url: "" },
      { id: "3", name: " ", url: "" },
      { id: "4", name: " ", url: "" },
      { id: "5", name: " ", url: "" }
    ]
